export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			userinfo: '',
			shuomingStatus: 2,
			zhizhao: 2,
		}
	},
	onLoad() {
		var that = this;
		// that.userinfo = uni.getStorageSync('userInfo');
		that.getuserdata();
	},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() { 
		this.getuserdata();
	},
	computed: {
		style() {
			var StatusBar = this.StatusBar;
			var CustomBar = this.CustomBar;
			var bgImage = this.bgImage;
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
			if (this.bgImage) {
				style = `${style}background-image:url(${bgImage});`;
			}
			return style
		}
	},
	onShareAppMessage() {
		var that = this;
		var title = that.userinfo.share.title;
		var path = '/pages/index/index?tid='+that.userinfo.share.id;
		var imageUrl = that.userinfo.share.image;
		return {
			title: title,
			path: path,
			imageUrl: imageUrl,
		}

	},
	methods: {
		getuserdata() {
			var that = this;
			this.$http.post("user/info").then((res) => {
				console.log("获取用户信息", res.data);
				that.userinfo = res.data;
				uni.setStorageSync('userinfo', res.data);
			})
		},
		// 执照弹窗
		alertzhizhao() {
			var that = this;
			if (that.zhizhao == 1) {
				that.zhizhao = 2;
			} else {
				that.zhizhao = 1;
			}
		},
		onGotUserInfo(res) {
			var that = this
			if (res.detail.userInfo) {
				uni.setStorageSync('userInfo', res.detail.userInfo);
				this.upUserInfo(res.detail, 1);
				that.getuserdata();
			}
		},
		addFormIDs(e) {
			this.addFormID(e.detail.formId);
		},
		cshuomingStatus(e) {
			console.log('切换说明显示状态')
			var that = this;
			that.addFormID(e.detail.formId);
			if (that.shuomingStatus == 1) {
				that.shuomingStatus = 2;
			} else {
				that.shuomingStatus = 1;
			}
		},
		// 提现
		tixian(e) {
			var that = this;
			this.addFormID(e.detail.formId);
			if(that.userinfo.userRow.money < 1){
				uni.showModal({
					title: '温馨提示',
					content: "满1元可提现",
					showCancel: false,
				})
				return;
			} 
			this.$http.post("user/tixian").then((res)=>{
				if(res.code == 1){
					that.getuserdata();	  
				}
				uni.showModal({
					title:'温馨提示',
					content:res.msg,
					showCancel:false
				})
			})
			
		},

		jumpTo(e) {
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},

		savehaibao() {
			var that = this;
			uni.hideLoading();
			var url = that.sharepics;
			
			that.picstatus = 1;
			
			uni.showToast({
				title: '保存中',
				icon: 'loading',
			})
			uni.downloadFile({
				url: url,
				success: function(res) {
					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: function(res) {
							console.log('baocun3');
							uni.previewImage({
								current: url,
								urls: [url],
								success: function(res) {
									console.log(res);
								},
							})

						},
						fail: function(res) {
							console.log('baocun3')
							uni.showToast({
								title: '保存失败',
								icon: 'none',
								success: function() {
									setTimeout(function() {
										uni.getSetting({
											success(res) {

												if (res.authSetting['scope.writePhotosAlbum'] == false) {
													uni.openSetting({
														success(res) {
															console.log(res.authSetting)
														}
													})
												}

											}
										})
									}, 1500)
								}
							})

						}
					})
				},
				fail: function() {
					console.log('fail')
				}
			})
		},
	}
}
